html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

html {
    font-size: 62.5%;
    height: 100vh;
    background: lightgrey;
}

a, button {
    text-decoration: none;
    color: black;
}

.option {
  margin: 0 auto;
  display: flex;
  justify-content: center;
}

.options-label {
  font-size: 1.8rem;
  margin-right: 1rem;
}


.black-text {
  color: black;
}

.red-text {
  color: red
}

#add-a-comment {
  display: block;
  font-size: 2.4rem;
  margin: 0 auto;
  cursor: pointer;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

#add-a-comment:hover{
  color: white;
  background: black;
  transition: 0.25s;
}

#expanded-comment-form {
  margin-top: 2rem;;
}

#comment-submit-button {
  display: block;
  margin: 0 auto;
  font-size: 1.8rem;
  cursor: pointer;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

#comment-submit-button:hover{
  color: white;
  background: black;
  transition: 0.25s;
}

#comment-textarea {
  width: 50rem;
  height: 15rem;
  font-family: Raleway, 'Times New Roman', Times, serif;
}

#comment-form {
  margin-top: 1.5rem;
}

#char-count {
  font-size: 1.4rem;
  margin-left: 0.5rem;
}

#comment-error-message {
  text-align: center;
  color: red;
  font-size: 1.8rem;
  margin-top: 0.5rem;
}

#comment-upload-message {
  text-align: center;
  color: black;
  font-size: 1.8rem;
  margin-top: 0.5rem;
}

#recaptcha-comment {
  display: block;
  margin: 1rem auto;
  max-width: max-content;
}

.rc-anchor-light {
  margin: 1rem auto;
  max-width: 95%;
}